'use strict';
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchData } from "./../redux/data/dataActions";
import i18n from 'i18next';
import { useTranslation } from 'react-i18next';

import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import GTranslateIcon from '@mui/icons-material/GTranslate';

function LangChange(props) {
  const dispatch = useDispatch();
  const { t } = useTranslation()

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const changeLanguage = async (key) => {
    i18n.changeLanguage(key); // en or zh
    handleClose()
  };

  return (
    <div style={{ cursor: 'pointer' }} >

      <GTranslateIcon
        onClick={handleClick}
        fontSize="large"
        sx={{ color:'#3f51b5', borderRadius: 1, border: 'solid 1px #eee', p: 1 }}
      />

      <Menu
        id="demo-positioned-menu"
        aria-labelledby="demo-positioned-button"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
      >
        <MenuItem onClick={() => { changeLanguage('en') }} value={'en'}>English</MenuItem>        
        <MenuItem onClick={() => { changeLanguage('zh') }} value={'zh'}>中文</MenuItem>
      </Menu>
    </div>
  );
}

export default LangChange;